import { useState } from 'react';

import { toast } from 'react-toastify';

import Button from '../Button/Button';
const SearchForm = ({ handelSubmit }) => {
  const [inputValue, setInputValue] = useState('');

  const updateinputValue = ({ target }) => {
    setInputValue(target.value);
  };
  // Ð¿Ð¾ ÑÐ°Ð±Ð¼Ñ–Ñ‚Ñƒ Ñ„Ð¾Ñ€Ð¼Ð¸ Ð²Ñ–Ð´Ð¿Ñ€Ð°Ð²Ð»ÑÑ”Ð¼ Ð·Ð½Ð°Ñ‡ÐµÐ½Ñ Ð² ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚ App
  const handelFormSubmit = event => {
    event.preventDefault();

    if (inputValue === '') {
      return toast.error('empty value type something', {
        theme: 'dark',
      });
    }

    handelSubmit(inputValue);

    // console.log(inputValue);
    // setInputValue('');
  };

  return (
    <form onSubmit={handelFormSubmit}>
      <input
        value={inputValue}
        onChange={updateinputValue}
        type="text"
        autoComplete="off"
        autoFocus
        placeholder="Search images and photos"
      />
      <Button type="submit" SearchFormButton>
        <span>Search</span>
      </Button>
    </form>
  );
};

export default SearchForm;
